//Inpust.js文件
//v-03版，传递点击事件及输入框内容
//v-04，增加一个输入参数（停止开单偏离点数）
//v-05（当前），
import React, { useState } from "react";
import { TextField, Button, Grid, Box, Typography } from "@mui/material";
import annotations from './commonParts/annotations';

export let newInputValues = []; //定义一个空的 可导出的 数组（用于储存 键入数据）

const Inputs = ({ onClick }) => {
  const [inputValues, setInputValues] = useState(Array(8).fill(""));
  const [inputRules] = useState([
    /^[a-zA-Z]+$/, //规则1  
    /^[0-9]+(\.[0-9]+)?/, // 规则3 - 小数
    /^[0-9]+$/, // 规则2
    /^[0-9]+$/, // 规则2
    /^[0-9]+$/, // 规则2
    /^[0-9]+$/, // 规则2
    /^([±]?\d+)|(-\d*)$/, // 规则4 - 大于或小于0的整数
    /^[0-9]+$/, // 规则2
    // ... 添加更多的规则
  ]);

  const handleInputChange = (event, index) => {
    const { value } = event.target;

    if (!validateInput(value, inputRules[index])) {
      event.preventDefault();
      return;
    }

    setInputValues((prevValues) => {
      const updatedValues = [...prevValues];
      updatedValues[index] = value;
      return updatedValues;
    });
  };

  const handleKeyDown = (event, index) => {
    if (
      (event.key === "Backspace" || event.key === "Delete") &&
      inputValues[index].length === 1
    ) {
      setInputValues((prevValues) => {
        const updatedValues = [...prevValues];
        updatedValues[index] = "";
        return updatedValues;
      });
    }
  };

  const handleConfirmation = () => {
    //传递数据
    //saveDataToLocalStorage(inputValues)
    newInputValues = [...inputValues];
    //console.log('Inputs.js文件  inputValues = ',newInputValues);
    // 调用传递进来的onClick函数
    onClick && onClick();
    //清除输入框的内容
    setInputValues(Array(8).fill(""));
  };

  const handleReset = () => {
    setInputValues(Array(8).fill(""));
    //console.log("重置输入框按钮点击");
  };

  const validateInput = (value, rule) => {
    if (!rule) {
      return true; // 没有规则时默认通过验证
    }

    const regex = new RegExp(rule);
    return regex.test(value);
  };

  const xs = 6;
  const sm = 3;

  return (
    <Box
      display="flex"
      flexDirection="column"
      alignItems="center"
      marginTop={3}
      textAlign="left"  //"center" // 将对齐方式设置为起始位置
    >
      <Typography variant="h5" component="h2">
        Set intelligent trading parameters for each Symbol
      </Typography>
      <br />{/* 在标题后添加一个空行 */}
      <Box marginTop={2}>
        <Grid container spacing={3}>
          <Grid item xs={xs} sm={sm}>
            <TextField
              label={annotations[0]}   //"货币"
              type="text"
              value={inputValues[0]}
              onChange={(event) => handleInputChange(event, 0)}
              onKeyDown={(event) => handleKeyDown(event, 0)} />
          </Grid>
          <Grid item xs={xs} sm={sm}>
            <TextField
              label={annotations[1]}  //"交易量"
              type="text"
              value={inputValues[1]}
              onChange={(event) => handleInputChange(event, 1)}
              onKeyDown={(event) => handleKeyDown(event, 1)} />
          </Grid>
          <Grid item xs={xs} sm={sm}>
            <TextField
              label={annotations[2]}  //"初始偏离点数"
              type="text"
              value={inputValues[2]}
              onChange={(event) => handleInputChange(event, 2)}
              onKeyDown={(event) => handleKeyDown(event, 2)} />
          </Grid>
          <Grid item xs={xs} sm={sm}>
            <TextField
              label={annotations[3]} //"固定止损点数"
              type="text"
              value={inputValues[3]}
              onChange={(event) => handleInputChange(event, 3)}
              onKeyDown={(event) => handleKeyDown(event, 3)} />
          </Grid>
          <Grid item xs={xs} sm={sm}>
            <TextField
              label={annotations[4]} //"固定止盈点数"
              type="text"
              value={inputValues[4]}
              onChange={(event) => handleInputChange(event, 4)}
              onKeyDown={(event) => handleKeyDown(event, 4)} />
          </Grid>
          <Grid item xs={xs} sm={sm}>
            <TextField
              label={annotations[5]} //"轮数"
              type="text"
              value={inputValues[5]}
              onChange={(event) => handleInputChange(event, 5)}
              onKeyDown={(event) => handleKeyDown(event, 5)} />
          </Grid>
          <Grid item xs={xs} sm={sm}>
            <TextField
              label={annotations[6]}  //"递增/递减"
              type="text"
              value={inputValues[6]}
              onChange={(event) => handleInputChange(event, 6)}
              onKeyDown={(event) => handleKeyDown(event, 6)} />
          </Grid>
          <Grid item xs={xs} sm={sm}>
            <TextField
              label={annotations[7]}  //"停止开单偏离点数"
              type="text"
              value={inputValues[7]}
              onChange={(event) => handleInputChange(event, 7)}
              onKeyDown={(event) => handleKeyDown(event, 7)} />
          </Grid>
          <Grid item xs={xs}>
            <Button
              onClick={handleConfirmation}
              disabled={inputValues.some(value => value === '')}
              variant="contained"
            >
              Confirm Inputs
            </Button>
            <Button
              onClick={handleReset}
              disabled={!inputValues.some(value => value !== '')}
              variant="contained"
              sx={{ marginLeft: "1rem" }}
            >
              Reset Inputs
            </Button>
          </Grid>
        </Grid>
      </Box>
    </Box>
  );
};

export default Inputs;