//输入数据页面，渲染完整版-02
//增加页面结尾文本（共设置了n个货币，如果...），《重置设置》、《退出设置》、《启动EA》共3个按键
//v-04：双击选项卡，删除，会弹窗警告选择
//v-05，新增资金管理数据输入, 
//v-06，连接根APP，删除全局变量newData
//v-07（当前）连接PopupWIndow.js
//TabbedData.js文件
import React, {useState } from 'react';
import { Box, Tabs, Tab, Typography, Grid, ListItem, ListItemText  } from '@mui/material';
import Inputs, { newInputValues } from './Inputs';
import InputsFund, { newInputFund } from './InputsFund';
import annotations from './commonParts/annotations'
import annotationsFund from './commonParts/annotationsFund'
import StyledButton from './commonParts/StyledButton'

//export let newData = []; 

const TabbedData = ({ onClick,onExitSettingsClick,onStartEAClick }) => {
  const [tabData, setTabData] = useState([]);
  const [value, setValue] = useState(0);
  const tabFundName = 'FundManagement';

  const updateTabData = (name, filteredValues) => {
    const foundIndex = tabData.findIndex((entry) => entry.name === name);
    const updatedEntry = {
      name: name,
      value: filteredValues,
    };
  
    if (foundIndex !== -1) {
      setTabData((prevData) => {
        const newData = [...prevData];
        newData.splice(foundIndex, 1); // 从原有位置删除
        newData.unshift(updatedEntry); // 添加到开头
        return [...newData];
      });
      setValue(0); // 更新选项卡值为新增选项卡的索引
    } else {
      setTabData((prevData) => [updatedEntry, ...prevData]); // 直接添加到开头
      setValue(0); // 更新选项卡值为新增选项卡的索引
    }
  };

  const handleFileInputsClick = () => {
    const name = newInputValues[0];

    const filteredValues = newInputValues.filter((value) => value !== '');

    if (filteredValues.length > 0) {
      updateTabData(name, filteredValues);
    }

    onClick && onClick();
  };

  const handleFileInputFundClick = () => {
    const name = tabFundName;

    const filteredValues = newInputFund.filter((value) => value !== '');

    if (filteredValues.length > 0) {
      updateTabData(name, filteredValues);
    }

    onClick && onClick();
  };

/*   useEffect(() => {
    newData = tabData;
  }, [tabData]); */

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  
  const EndText = () => (
    <Box sx={{ m: 2 }}>
      <Typography>
        {tabData.length > 0 && (
          <>
            There are a total of {tabData.length} set.
            <br />
          </>
        )}
        If the parameters are confirmed to be correct, click on the "Start EA" button.<br/>
        If you need to reset all parameters, please click on the "Reset Settings" button.<br/>
        To abandon and exit the settings, please click on the "Exit Settings" button.
      </Typography>
      <Box sx={{ display: 'flex', justifyContent: 'flex-end', mt: 2 }}>
        <StyledButton
          variant="contained"
          onClick={handleStartEAClick}
          disabled={tabData.length < 2 || !tabData.some(entry => entry.name === tabFundName)}
        >
          Start EA
        </StyledButton>
        <Box sx={{ mx: 1 }}></Box>
        <StyledButton 
          variant="contained" onClick={handleResetSettingsClick} disabled={tabData.length<1}>Reset Settings</StyledButton>
        <Box sx={{ mx: 1 }}></Box>
        <StyledButton variant="contained" onClick={handleExitSettingsClick}>Exit Settings</StyledButton>
      </Box>
    </Box>
  );



  const handleResetSettingsClick = () => {
    setTabData([]); 
  };
  
  const handleStartEAClick = () => {
    //点击‘启动EA’键
    onStartEAClick(tabData); // 调用父组件的onStartEAClick函数，并传递tabData
    setTabData([]);
  };

  const handleExitSettingsClick = () => {
    //点击‘退出设置’键
    onExitSettingsClick(); // 不带回数据
    setTabData([]);
  };

  const handleTabDoubleClick = (index) => {
    const confirmDelete = window.confirm("Are you sure you want to delete this tab?");
    if (confirmDelete) {
      // 创建一个新的选项卡数组，排除当前选中的选项卡
      const updatedTabData = tabData.filter((_, i) => i !== index);
      // 更新组件状态
      setTabData(updatedTabData);
      // 如果删除的是当前选中的选项卡，且还有剩余选项卡，则更新选项卡值为最后一个选项卡的索引；否则重置选项卡值为第一个选项卡的索引
      setValue(index === value && updatedTabData.length > 0 ? updatedTabData.length - 1 : 0);
    }
  };



  return (
    <Box sx={{ width: '100%' , zIndex: 9999 }}>
      <div>
        <Inputs onClick={handleFileInputsClick} />
      </div>
      <Tabs value={value} onChange={handleChange} aria-label="Tabs">
        {tabData.map((entry, index) => (
          <Tab key={index} label={entry.name} onDoubleClick={() => handleTabDoubleClick(index)}/>
        ))}
      </Tabs>

      <Typography component="div" role="tabpanel" hidden={tabData.length === 0}>
        <Box sx={{ p: 2 }}>
          {tabData.map((item, itemIndex) => (
            <Grid
              key={itemIndex}
              container
              spacing={2}
              sx={{
                display: itemIndex === value ? 'flex' : 'none',
                flexDirection: 'row',
              }}
            >
              {item.value.map((value, valueIndex) => (
                <Grid item xs={3} key={`${itemIndex}-${valueIndex}`}>
                  <ListItem sx={{ px : 1 }}>
                    <ListItemText
                      primary={ tabData[itemIndex].name === tabFundName ? annotationsFund[valueIndex] : annotations[valueIndex] }
                      secondary={`Value: ${value}`}
                      secondaryTypographyProps ={{ component: 'div' }}
                    />
                  </ListItem>
                </Grid>
              ))}
            </Grid>
          ))}
        </Box>
      </Typography>

      <Typography component="div" role="tabpanel" hidden={tabData.length === 0}>
        <Box sx={{ p: 2 }}>
          {/* ... */}
        </Box>
      </Typography>
        <div>
          <InputsFund onClick={handleFileInputFundClick} />
        </div>

      <EndText />

    </Box>
  );
};

export default TabbedData;
