import React, { useState, useEffect } from "react";
import Draggable from "react-draggable";
import Button from "@mui/material/Button";

const InitialButton = ({ onInitClick, showEA }) => {
  //const storedPos = JSON.parse(localStorage.getItem("startDragPos")) || { x: 0, y: 0 };
  const storedPos = { x: 0, y: 0 };

  const [startDragPos, setStartDragPos] = useState(storedPos);

  useEffect(() => {
    localStorage.setItem("startDragPos", JSON.stringify(startDragPos));
  }, [startDragPos]);

  const handleMouseDown = (e) => {
    const { clientX, clientY } = e;
    setStartDragPos({ x: clientX, y: clientY });
  };

  const handleMouseUp = (e) => {
    const { clientX, clientY } = e;
    const { x: startX, y: startY } = startDragPos;
    const dragThreshold = window.innerWidth * 0.01;
    if (
      Math.abs(clientX - startX) < dragThreshold &&
      Math.abs(clientY - startY) < dragThreshold
    ) {
      if (showEA) {
        togglePopup();
      }
    }
  };

  const togglePopup = () => {
    //setShowPopup(!showPopup);
    const handleMouse = (e) => {
      const { clientX, clientY } = e;
      setStartDragPos({ x: clientX, y: clientY });
    };
  };
  
  const handleDoubleClick = () => {
    onInitClick();
  };

  return (
    <div>
      <Draggable onStart={handleMouseDown} onStop={handleMouseUp}>
        <Button
          onDoubleClick={handleDoubleClick}
          style={{
            background: showEA ? `linear-gradient(135deg, #B8D26D, #5B8930)` : "",
          }}
        >
          Expert Advisor
        </Button>
      </Draggable>
    </div>
  );
};

export default InitialButton;