// NewComponent.js文件
import React from "react";
import Dialog from "@mui/material/Dialog";
import Button from "@mui/material/Button";
import { styled } from "@mui/system";

const CustomDialog = styled(Dialog)({
  display: "flex",
  alignItems: "center",
  justifyContent: "center",
});

const CustomDialogContent = styled("div")({
  backgroundColor: "#fff",
  border: "2px solid #000",
  padding: "20px",
  width: "400px",
});

const ButtonsContainer = styled("div")({
  display: "flex",
  justifyContent: "space-between",
  marginTop: "20px",
});

const NewComponent = ({ onConfirm, confirmText , onCancel }) => {
  const pText = (confirmText === undefined) ? 'Are you sure you want to exit?' : confirmText ;
    
  const handleConfirm = () => {
    onConfirm(true);
  };

  const handleCancel = () => {
    onCancel(false);
  };

  return (
    <CustomDialog open>
      <CustomDialogContent>
        <p>{pText}</p>

        <ButtonsContainer>
          <Button onClick={handleConfirm}>Confirm</Button>
          <Button onClick={handleCancel}>Cancel</Button>
        </ButtonsContainer>
      </CustomDialogContent>
    </CustomDialog>
  );
};

export default NewComponent;