
//TypographyInfo.js
import React, { useState, useEffect, useRef } from "react";
import { Box, Collapse, Typography } from "@mui/material";

const TypographyInfo = ({ windowScale , showText}) => {
  const [isTypographyOpen, setIsTypographyOpen] = useState(false);
  const [lastClickTime, setLastClickTime] = useState(0);

  const handleScaleInputChange = () => {
    setIsTypographyOpen(true);
    setLastClickTime(Date.now());
  };

  useEffect(() => {
    if(windowScale!==100) //默认值100不显示
      handleScaleInputChange();
  }, [windowScale, showText]);

  const timerRef = useRef(null);
  useEffect(() => {
    timerRef.current = setTimeout(() => {
      setIsTypographyOpen(false);
    }, 2000);

    return () => clearTimeout(timerRef.current);
  }, [lastClickTime]);

  const boxText =
    windowScale === undefined
      ? showText
      : `The current window accounts for ${windowScale}% of the visible screen`;

  return (
    <Collapse in={isTypographyOpen}>
      <Box
        sx={{
          background: `linear-gradient(135deg, #B8D26D, #5B8930)`,
          padding: "12px",
          position: "fixed",
          alignItems: "center",
          top: "50%",
          left: "50%",
          transform: "translate(-50%, -50%)",
          transformOrigin: "center",
          width: "fit-content",
      }}
      >
        <Typography>{boxText}</Typography>
      </Box>
    </Collapse>
  );
};

export default TypographyInfo;



/**
    然后在父件中使用该子组件：

    ```jsx
    import TypographyInfo from "./TypographyInfo";

    // 父组件的代码....

    return (
    // 父组件的 JSX
    <div>
        {/* 其他组件内容 /}
        <TypographyInfo windowScale={windowScale} />
    </div>
    );
*/